import React, { createContext, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { jwtDecode } from 'jwt-decode';
import { API_BASE_URL } from '../config';


// https://zenn.dev/idapan/articles/0a2053e54b41e0
const AuthContext = createContext({
    user: null,
    authToken: null,
    loginUser: async () => { console.error("loginUser"); },
    logoutUser: () => { console.error("logoutUser"); },
});

export default AuthContext;

//
export const AuthProvider = ({ children }) => {
    const getInitialAuthToken = () => {
        try {
            const token = localStorage.getItem('authToken');
            return token ? JSON.parse(token) : null;
        } catch (error) {
            console.error("localStorageauthToken", error);
            return null;
        }
    };

    const [authToken, setAuthToken] = useState(getInitialAuthToken);
    //
    //authTokenauthToken.accessjwtuser
    const [user, setUser] = useState(() => (authToken ? jwtDecode(authToken.access) : null));
    const [loading, setLoading] = useState(true);
    const navigate = useNavigate();
//asyncawait(API)
//await
//axios.postresponseif (response.status === 200)
    const loginUser = async (username, password) => {
        setLoading(true);
        try {
            const response = await axios.post(`${API_BASE_URL}/token/`, {
                username: username,
                password: password,
            });
            if (response.status === 200) {
                const data = response.data;
                setAuthToken(data);
                const decodedUser = jwtDecode(data.access);
                console.log("Decoded JWT:", decodedUser); // Log decoded token for debugging
                setUser(decodedUser);
                localStorage.setItem('authToken', JSON.stringify(data));
                setLoading(false);
                return decodedUser; // 
            }
            setLoading(false);
            return null; // 
        } catch (error) {
            console.error('Login:', error);
            alert('');
            setLoading(false);
            return null; // 
        }
    };

    // 
    const logoutUser = () => {
        setAuthToken(null);
        setUser(null);
        localStorage.removeItem('authToken');
        navigate('/login');
    };


//
    const contextData = {
        user,
        authToken,
        loading,
        loginUser,
        logoutUser,
    };
    
    // 
    // 
    useEffect(() => {
        if (authToken) {
            setUser(jwtDecode(authToken.access));
        } else {
            setUser(null);
        }
        setLoading(false);
    }, [authToken]);


    return (
//loading
//Flickerby gemini        
        <AuthContext.Provider value={contextData}>
            {children}
        </AuthContext.Provider>
    );
};

