import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  Box,
  Paper,
  Typography,
  Button as MuiButton,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  TextField
} from '@mui/material';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { StaticTimePicker } from '@mui/x-date-pickers/StaticTimePicker';
import dayjs from 'dayjs';
import { API_BASE_URL } from '../config';

export default function TableSet() {
  const { tableId } = useParams();
  const navigate = useNavigate();

  const [status, setStatus] = useState('available');
  const [people, setPeople] = useState('');
  const [entryTime, setEntryTime] = useState(dayjs());
  const [memo, setMemo] = useState('');
  const [extensionMinutes, setExtensionMinutes] = useState(0);

  const fetchTableData = useCallback(async () => {
    try {
      const response = await axios.get(`${API_BASE_URL}/tables/${tableId}/`);
      const data = response.data;
      setStatus(data.status || 'available');
      setPeople(data.people || '');
      setEntryTime(data.entryTime ? dayjs(data.entryTime) : dayjs());
      setMemo(data.memo || '');
      setExtensionMinutes(data.extension_minutes || 0);
    } catch (error) {
      console.error("Failed to fetch table data:", error);
    }
  }, [tableId]);

  useEffect(() => {
    fetchTableData();
  }, [fetchTableData]);

  const makeApiRequest = async (payload, options = {}) => {
    const { navigateHome = false, alertMessage = null } = options;
    try {
      await axios.patch(`${API_BASE_URL}/tables/${tableId}/`, payload);
      if (alertMessage) alert(alertMessage);
      if (navigateHome) {
        navigate('/');
      } else {
        fetchTableData(); // Refresh data if not navigating away
      }
    } catch (error) {
      console.error("API request failed:", error);
      alert("");
    }
  };

  const handleUpdate = () => {
    const payload = {
      status,
      people: people === '' ? null : Number(people),
      entryTime: entryTime.toISOString(),
      memo,
    };
    makeApiRequest(payload, { navigateHome: true });
  };

  const handleExit = () => {
    const payload = {
      status: 'available',
      people: null,
      entryTime: null,
      exitTime: new Date().toISOString(),
      memo: '',
      extension_minutes: 0,
    };
    makeApiRequest(payload, { navigateHome: true });
  };

  const handleExtend = () => {
    const newExtensionTime = extensionMinutes + 30;
    makeApiRequest({ extension_minutes: newExtensionTime }, { alertMessage: '30' });
  };

  return (
    <Paper sx={{ p: 4, maxWidth: 600, margin: 'auto' }} elevation={3}>
      <Typography variant="h4" gutterBottom>
        {tableId}
      </Typography>

      {/*  */}
      <FormControl fullWidth sx={{ mt: 4 }}>
        <InputLabel id="status-select-label"></InputLabel>
        <Select
          labelId="status-select-label"
          value={status}
          label=""
          onChange={(e) => setStatus(e.target.value)}
        >
          <MenuItem value="occupied"></MenuItem>
          <MenuItem value="available"></MenuItem>
          <MenuItem value="reserved"></MenuItem>
        </Select>
      </FormControl>

      {/*  */}
      <FormControl fullWidth sx={{ mt: 3 }}>
        <TextField
          label=""
          type="number"
          value={people}
          onChange={(e) => setPeople(e.target.value)}
        />
      </FormControl>
      
      {/*  */}
      <Box sx={{ mt: 3 }}>
        <Typography variant="subtitle1" gutterBottom></Typography>
        <LocalizationProvider dateAdapter={AdapterDayjs}>
          <StaticTimePicker
            orientation="landscape"
            value={entryTime}
            onChange={(newValue) => setEntryTime(newValue)}
          />
        </LocalizationProvider>
      </Box>

      {/*  */}
      <Box sx={{ mt: 3 }}>
        <Typography variant="subtitle1" gutterBottom></Typography>
        <TextField
          fullWidth
          multiline
          rows={3}
          value={memo}
          onChange={(e) => setMemo(e.target.value)}
          variant="outlined"
        />
      </Box>


      <Box sx={{ mt: 4, display: 'flex', gap: 1, justifyContent: 'flex-end' }}>
        <MuiButton
          variant="outlined"
          color="error"
          onClick={handleExit}
          disabled={status !== 'occupied'}
        >
          
        </MuiButton>
        <MuiButton
          variant="contained"
          color="secondary"
          onClick={handleExtend}
          disabled={status !== 'occupied'}
        >
          30
        </MuiButton>
        <MuiButton
          variant="contained"
          onClick={handleUpdate}
        >
          
        </MuiButton>
        <MuiButton
          variant="outlined"
          onClick={() => navigate('/')}
        >
          
        </MuiButton>
      </Box>
    </Paper>
  );
}

